.select-wrapper-small{
  margin: 0 auto;
  width: 40%;
  font-size: .5em;
}
.select-wrapper-medium{
  margin: 0 auto;
  width: 65%;
  font-size: .9em;
}
.select-wrapper-large{
  margin: 0 auto;
  width: 100%;
}
.select-container {
  
  color: #FFF;
  background-color: #3A3C48;
  margin: 1em;
  padding: .5em;
  max-height: 400px;
  overflow-y: scroll;
}
.select {

  padding: .5em;
  border-radius: 10px;
  font-size: 1.2em;
  display: flex;
  justify-content: space-between; 
}

.select-form {
  width: 100%;
  display: flex;
  flex-direction: column;
  margin: .5em auto;
}
.select-form input {
  display: none;
}
.label {
  border-radius: 5px;
}
.label:hover{
  background: #e7e7e7;
  color: #3A3C48;
}
.select-form-open{
  display: none;
}